import Long from '../../node_modules/long/dist/long';
var handleMessage = function(meta, status, conn) {
    var self = conn;
    var messageBodyMessage = self.context.root.lookup("easemob.pb.MUCBody");
    var thirdMessage = messageBodyMessage.decode(meta.payload);
    var msgId = new Long(meta.id.low, meta.id.high, meta.id.unsigned).toString();
    var operation = thirdMessage.operation;

    function condition(operation) {
        let info = {
            type: '',
            owner: thirdMessage.from.name,
            gid: thirdMessage.mucId.name,
            from: thirdMessage.from.name,
            fromJid: thirdMessage.from,
            to: thirdMessage.to.length ? thirdMessage.to[0].name : '',
            toJid: thirdMessage.to,
            chatroom: thirdMessage.isChatroom,
            status: thirdMessage.status
        }

        return ({
            32: () => { ///
                info.type = thirdMessage.isChatroom ? 'rmChatRoomMute' : 'rmGroupMute';
                conn.onPresence(info);
            },
            31: () => { ///
                info.type = thirdMessage.isChatroom ? 'muteChatRoom' : 'muteGroup';
                conn.onPresence(info);
            },
            30: () => { ///
                info.type = thirdMessage.isChatroom ? 'rmUserFromChatRoomWhiteList' : 'rmUserFromGroupWhiteList';
                conn.onPresence(info);
            },
            29: () => { ///
                info.type = thirdMessage.isChatroom ? 'addUserToChatRoomWhiteList' : 'addUserToGroupWhiteList';
                conn.onPresence(info);
            },
            28: () => {
                info.type = 'deleteFile'; // 
                conn.onPresence(info);
            },
            27: () => {
                info.type = 'uploadFile'; // 
                conn.onPresence(info);
            },
            26: () => {
                info.type = 'deleteAnnouncement'; //
                conn.onPresence(info);
            },
            25: () => {
                info.type = 'updateAnnouncement'; // 
                conn.onPresence(info);
            },
            24: () => {
                info.type = 'removeMute'; //
                conn.onPresence(info);
            },
            23: () => {
                info.type = 'addMute'; //
                conn.onPresence(info);
            },
            22: () => {
                info.type = 'removeAdmin'; //
                conn.onPresence(info);
            },
            21: () => {
                info.type = 'addAdmin'; //
                conn.onPresence(info);
            },
            20: () => {
                info.type = 'changeOwner'; //
                conn.onPresence(info);
            },
            19: () => {
                info.type = 'direct_joined'; //
                conn.onPresence(info);
            },
            18: () => {
                info.type = thirdMessage.isChatroom ? 'leaveChatRoom' : 'leaveGroup';
                //info.type = 'absence'; //
                conn.onPresence(info);
            },
            17: () => {
                info.type = thirdMessage.isChatroom ? 'memberJoinChatRoomSuccess' : 'memberJoinPublicGroupSuccess';
                //info.type = 'presence'; // 
                conn.onPresence(info);
            },
            16: () => {
                info.type = 'unblock';
                conn.onPresence(info);
            },
            15: () => {
                info.type = 'block';
                conn.onPresence(info);
            },
            14: () => {
                info.type = 'update';
                conn.onPresence(info);
            },
            13: () => {
                info.type = 'allow'; //
                info.reason = thirdMessage.reason;
                conn.onPresence(info);
            },
            12: () => {
                info.type = 'ban';
                conn.onPresence(info);
            },
            11: () => {
                info.type = 'getBlackList';
                conn.onPresence(info);
            },
            10: () => {
                info.type = 'removedFromGroup'; // 
                info.kicked = info.to;
                conn.onPresence(info);
            },
            9: () => {
                info.type = 'invite_decline'; //
                info.kicked = info.to;
                conn.onPresence(info);
            },
            8: () => {
                info.type = 'invite_accept'; //
                info.kicked = info.to;
                conn.onPresence(info);
            },
            7: () => {
                info.type = 'invite'; // "INVITE_ACCEPT": 8, "INVITE_DECLINE": 9,
                info.kicked = info.to;
                conn.onPresence(info);
            },
            6: () => {
                info.type = 'joinPublicGroupDeclined'; //
                conn.onPresence(info);
            },
            5: () => {
                info.type = 'joinPublicGroupSuccess'; //
                conn.onPresence(info);
            },
            4: () => {
                info.type = 'joinGroupNotifications'; //
                info.reason = thirdMessage.reason;
                conn.onPresence(info);
            },
            3: () => {
                info.type = 'leave';
                conn.onPresence(info);
            },
            2: () => {
                info.type = 'join';
                conn.onPresence(info);
            },
            1: () => {
                info.type = 'deleteGroupChat'; //
                conn.onPresence(info);
            },

        }[operation] || function() {
            console.log(`%c${operation}`, 'background: green')
        })()
    }

    condition(operation)
}

export default handleMessage